.navbar-nav {
  position: absolute;
  top: 40%;
  right: 20px;
  list-style: none;
  text-align: center;
}

.nav-item {
  position: relative;
  display: inline-block;
  padding-right: 20px;
  padding-left: 20px;
  padding-top: 9px;

}

.nav-link {
  text-decoration: none;
  position: relative;
  color: black;
}

.nav-link:hover {
  text-decoration: none;
  color: black;
}

.nav-link:after {
  content: '';
  position: absolute;
  width: 0; height: 3px;
  display: block;
  margin-top: 0px;
  right: 0;
  background: #e96188;
  transition: width .5s ease;
  -webkit-transition: width .5s ease;
}

.nav-link:hover:after{
  width: 100%;
  left: 0;
  background: #e96188;
}

.nav-item:hover {
  text-decoration: none;
}

